/**
 * Conditional form elements reveal
 *
 * Reveal content based on value of related field
 *
 * -----------------------------------------------------------------------
 * Usage:
 *
 * <input type="radio" name="trigger_name" value="regex">
 * <input type="radio" name="trigger_name" value="another value">
 * <div id="target_id" data-conditional="trigger_name" data-conditional-value="^regex$">...</div>
 */

(function() {
  'use strict';

  window.moj = window.moj || { Modules: {}, Events: $({}) };

  var Conditional = function($el, options) {
    this.init($el, options);
    return this;
  };

  Conditional.prototype = {
    defaults: {},

    init: function($el, options) {
      this.settings = $.extend({}, this.defaults, options);
      this.cacheElements($el);
      this.addAriaAttributes();
      this.bindEvents();
    },

    refresh: function() {
      this.cacheElements(this.$conditional);
      this.bindEvents();
    },

    cacheElements: function($el) {
      this.$conditional = $el;
      this.$inputs = $('[name="' + $el.data('conditional') + '"]');
    },

    bindEvents: function() {
      var self = this;
      this.$inputs
        .on('change.Conditional', function() {
          self.toggle();
        });
      moj.Events
        .on('render.Conditional', function() {
          self.toggle();
        });
    },

    addAriaAttributes: function() {
      var conditionalId = this.$conditional.attr('id');
      this.$inputs.not('[aria-controls*=' + conditionalId + ']').each(function() {
        $(this).attr('aria-controls', function(i, currentAttribute) {
          return currentAttribute ? currentAttribute + ' ' + conditionalId : conditionalId;
        });
      });
      this.$conditional.attr('aria-live', 'polite');
    },

    getInputValue: function($input) {
      switch($input.attr('type')) {
        case "radio":
          return $('[name="' + $input.attr('name') + '"]:checked').attr('value');

        case "checkbox":
          return $input.filter(':checked').val();

        default:
          return $input.val() || $input.find(':selected').attr('value');
      }
    },

    toggle: function() {
      var currentValue = this.getInputValue(this.$inputs),
          testExpression = new RegExp(this.$conditional.data('conditionalValue'));

      if (currentValue && currentValue.match(testExpression)) {
        this.$conditional
          .show()
          .attr('aria-expanded', 'true')
          .removeAttr('aria-hidden')
          .removeProp('hidden');
      }
      else {
        this.$conditional
          .hide()
          .attr('aria-expanded', 'false')
          .attr('aria-hidden', 'true')
          .prop('hidden', true);
      }
    }
  };

  moj.Modules._Conditional = Conditional;

  moj.Modules.Conditional = {
    init: function() {
      return $('[data-conditional]').each(function() {
        $(this).data('Conditional', new Conditional($(this), $(this).data()));
      });
    }
  };

}());

/**
 * Details Element
 *
 * Replicate <details> functionality that's entirely cross-browser
 * compatible and offers options to update text depending on state
 *
 * -----------------------------------------------------------------------
 * Usage:
 *
 * <div class="js-Details [open]" data-summary-open="Alternative text">
 *   <a class="details-trigger" href="#details-content"><span class="summary">View details</span></a>
 *   <div class="details-content" id="details-content">
 *     Content here
 *   </div>
 * </div>
 *
 * If summaryOpen is set, the summary text is replaced when open. Copy in markup
 * should always be for the closed state.
 */
(function() {
  "use strict";

  window.moj = window.moj || { Modules: {}, Events: $({}) };

  var Details = function($el, options) {
    this.init($el, options);
    return this;
  };

  Details.prototype = {

    init: function($el, options) {

      $("<i>").addClass("arrow arrow-closed").text("\u25ba").prependTo($('.details-trigger', $el));

      this.cacheElements($el);
      this.addAriaAttributes();
      this.bindEvents();

      this.isOpera = Object.prototype.toString.call(window.opera) == "[object Opera]";

      this.openText = this.$details.data("summary-open");
      this.closedText = this.$summaryText.text();

      this.updateState();
    },

    cacheElements: function($el) {
      this.$details = $el;
      this.$summary = $(".details-trigger", $el);
      this.$summaryText = $(".summary", this.$summary);
      this.$content = $(".details-content", $el);
      this.$icon = $("i.arrow", this.$summary);
    },

    addAriaAttributes: function() {
      var id = this.$content.attr("id");

      if (!id) {
        id = "js-details-" + this.$details.index(".js-Details");
        this.$content.attr("id", id);
      }

      this.$summary.attr({
        "role": "button",
        "aria-controls": id,
        "aria-expanded": "false"
      });

      this.$content.attr({
        "aria-hidden": "true",
        "aria-live": "polite"
      });
    },

    bindEvents: function() {
      var self = this;

      this.$summary.off("click.details keydown.details").on({
        "click.details": function(event) {
          event.preventDefault();
          self.toggleContent();
        },
        "keydown.details": function(event) {
          if (event.keyCode == 32 || (event.keyCode == 13 && !self.isOpera)) {
            event.preventDefault();
            self.$summary.click();
          }
        }
      });
    },

    updateState: function() {
      if (this.$details.hasClass("open")) {
        this.$summary.attr("aria-expanded", "true");
        this.$content.show().removeAttr("aria-hidden").removeProp('hidden');
        this.$icon.removeClass("arrow-closed").addClass("arrow-open").text("\u25bc");

        if (this.openText) {
          this.$summaryText.text(this.openText);
        }
      }
      else {
        this.$summary.attr("aria-expanded", "false");
        this.$content.hide().attr("aria-hidden", "true").prop("hidden", true);
        this.$icon.removeClass("arrow-open").addClass("arrow-closed").text("\u25ba");

        if (this.openText) {
          this.$summaryText.text(this.closedText);
        }
      }
    },

    toggleContent: function() {
      this.$details.toggleClass("open");
      this.updateState();
    }
  };

  moj.Modules._Details = Details;

  moj.Modules.Details = {
    init: function() {
      return $(".js-Details").each(function() {
        $(this).data("Details", new Details($(this), $(this).data()));
      });
    }
  };

}());

(function() {
  "use strict";

  window.moj = window.moj || { Modules: {}, Events: $({}) };

  var ExternalLinksTracker = function($el, options) {
    this.init($el, options);
    return this;
  };

  ExternalLinksTracker.prototype = {
    defaults: {
      "eventCategory": "External links",
      "eventAction": document.location.pathname
    },

    init: function($el, options) {
      if (typeof ga === "undefined") return;

      this.settings = $.extend({}, this.defaults, options);
      this.cacheElements($el);
      this.bindEvents();
    },

    cacheElements: function($el) {
      this.$links = $el;
    },

    bindEvents: function() {
      var self = this;

      this.$links.on('click', function(event) {
        event.preventDefault();
        self.sendGAEvent($(this));
      });
    },

    sendGAEvent: function($el) {
      var self = this,
          href = $el.attr("href"),
          target = $el.attr("target");

      ga("send",
         "event",
         self.settings.eventCategory,
         href,
         self.settings.eventAction,
         {"hitCallback": self.openLink(href, target)}
      );
    },

    openLink: function(href, target) {
      if (target && !/^_(self|parent|top)$/i.test(target)) {
        window.open(href, target);
      }
      else {
        window.location.href = href;
      }
    }
  };

  moj.Modules._ExternalLinksTracker = ExternalLinksTracker;

  moj.Modules.ExternalLinksTracker = {
    init: function() {
      $('a[rel=external]').each(function() {
        $(this).data('ExternalLinksTracker', new ExternalLinksTracker($(this), $(this).data()));
      });
    }
  };
}());

/**
 * Handle several aspects of focusing on the relevant element
 */
(function() {
  'use strict';

  window.moj = window.moj || { Modules: {}, Events: $({}) };

  moj.Modules.FocusHandler = {
    init: function() {
      var $firstAlert = $('.success-header, .error-summary').eq(0);

      /**
       * Set focus to the first alert or success on the page
       */
      this.addTabIndex($firstAlert, '0');
      $firstAlert.focus();

      /**
       * Set focus to the main content when clicking the 'skip link'
       */
      this.addTabIndex($('#content'), '-1');
      $('.skiplink').on('click', function() { $('#content').focus(); });

      /**
       * Set focus to the relevant field when clicking an error message in the summary
       */
      $('.error-summary [href^=#section_]').on('click', function() {
        var $target = $($(this).attr('href')).find('input, textarea, select').eq(0);
        setTimeout(function() { $target.focus(); }, 250);
      });
    },
    addTabIndex: function($el, tabIndex) {
      if ($el.attr('tabindex') === undefined) {
        $el.attr('tabindex', tabIndex);
      }
    }
  };
}());

/**
 * Page exit prompt
 *
 * Prompts the user upon leaving the page if they have changed
 * any data in any form.
 *
 * To set an alternative message or a session timeout timestamp
 * (after which the prompt will be disabled), declare the following
 * variables somewhere in the markup:
 *
 * <script>
 *   var promptOnChangeMessage = "Alternative message";
 *   var sessionTimeout = 123456789;
 * </script>
 */

(function(){
  'use strict';

  window.moj = window.moj || { Modules: {}, Events: $({}) };

  var PromptOnChange = function() {
    this.init();
    return this;
  };

  PromptOnChange.prototype = {
    init: function() {
      this.message = "You have entered some information";
      this.sessionTimeout = false;

      if (typeof promptOnChangeMessage === 'string') {
        this.message = promptOnChangeMessage;
      }
      if (typeof sessionTimeout === 'number') {
        this.sessionTimeout = sessionTimeout;
      }

      this.currentHash = this.hashFields();
      this.bindEvents();

      this.enable();
    },

    bindEvents: function() {
      var self = this;

      $('form').on('submit', function() {
        self.disable();
      });

      // The second event here is used for testing, as there is no
      // other way of mocking a beforeunload event!
      $(window).on('beforeunload mock.beforeunload', function() {
        return self.runCheck();
      });
    },

    hashFields: function() {
      return $('form').serialize();
    },

    fieldsHaveChanged: function() {
      return this.hashFields() !== this.currentHash;
    },

    runCheck: function() {
      if (this.isSessionTimeoutRedirect()) {
        this.disable();
      }

      if (this.isEnabled && this.fieldsHaveChanged()) {
        return this.message;
      }
    },

    enable: function() {
      this.isEnabled = true;
    },

    disable: function() {
      this.isEnabled = false;
    },

    isSessionTimeoutRedirect: function() {
      if (this.sessionTimeout) {
        var now = Math.floor(new Date().getTime() / 1000);

        if (now >= this.sessionTimeout) {
          return true;
        }
      }

      return false;
    }
  };

  moj.Modules._PromptOnChange = PromptOnChange;

  moj.Modules.PromptOnChange = {
    init: function() {
      return new PromptOnChange();
    }
  };

}());

/**
 * Selection Buttons
 *
 * Provides extra focused and selected classes to radio and checkbox
 * form inputs to bring them to default gov.uk styling
 */

(function() {
  'use strict';

  window.moj = window.moj || { Modules: {}, Events: $({}) };

  moj.Modules.SelectionButtons = {
    init: function() {

      $('label input[type=radio], label input[type=checkbox]').on({
        'change': function(){
          if ($(this).is(':checked')) {
            if ($(this).is(':radio')) {
              $(this).closest('form').find('[name="' + $(this).attr('name') + '"]').closest('label').removeClass('selected');
            }
            $(this).closest('label').addClass('selected');
          }
          else {
            $(this).closest('label').removeClass('selected');
          }
        },
        'focus': function() {
          $(this).closest('label').addClass('focused');
        },
        'blur': function() {
          $(this).closest('label').removeClass('focused');
        }
      }).trigger('change');

    }
  };

}());

/**
 * Templated Element
 *
 * Update the text of an element based on the value of a related input field
 *
 * -----------------------------------------------------------------------
 * Usage:
 *
 * <span id="templated">Original content</span>
 *
 * <script>
 * $(function(){
 *   options = {
 *     "trigger": "field_name",
 *     "templates": {
 *       "value_one": "Template one",
 *       "value_two": "Template two"
 *     }
 *   }
 *   new moj.Modules.TemplatedElement($('#templated'), options);
 * });
 * </script>
 */
(function() {
  'use strict';

  window.moj = window.moj || { Modules: {}, Events: $({}) };

  var TemplatedElement = function($el, options) {
    this.init($el, options);
    return this;
  };

  TemplatedElement.prototype = {
    defaults: {
      trigger: ''
    },

    init: function($el, options) {
      this.settings = $.extend({}, this.defaults, options);

      this.trigger = this.settings.trigger;
      this.templates = this.settings.templates;
      this.originalText = $el.eq(0).text();

      this.cacheElements($el);

      this.bindEvents();
    },

    cacheElements: function($el) {
      this.$element = $el;
      this.$inputs = $(':radio' + this.trigger);
    },

    bindEvents: function() {
      var self = this;
      this.$inputs.on('change.TemplatedElement', function() {
        self.updateText();
      });
      moj.Events.on('render.TemplatedElement', function() {
        self.updateText();
      });
    },

    getCurrentValue: function() {
      return this.$inputs.filter(':checked').val();
    },

    updateText: function() {
      this.$element.text(this.getText());
    },

    getText: function() {
      var text = this.originalText,
          value = this.getCurrentValue();

      if (value && this.templates.hasOwnProperty(value)) {
        text = this.templates[value];
      }

      return text;
    }
  };

  moj.Modules._TemplatedElement = TemplatedElement;

}());


$(function() {
  jQuery.fx.off = true;

  $('.nojs-only[name=split_form]').remove();

  // Handle spacebar use on links with role=button
  $(document).on('keypress', 'a[role=button]', function(event){
    if (event.keyCode === 32) {
      event.preventDefault();
      event.target.click();
    }
  });

  moj.init();
});
